import React, {useState, useEffect} from 'react';
import '../../css/AudioTrack.css'
import Player from "react-howler-player";
import axios from 'axios'
import { useHistory } from "react-router-dom";
import { url } from '../../config'

function RegisterComponent() {

    const [email, setEmail] = useState('')
    const [password, setPassword] = useState('')
    const [name, setName] = useState('')
    const history = useHistory();

    const attemptRegister=()=>{
        
        if (email && password && name) { // basic check to see that they are not empty
           // console.log(email, password, name)
           let data = {email: email, password: password, name: name}
           axios
            .post(`${url}/createUser`, data) // data to send
            .then(response => response)
            .then(data=> {
                console.log("Successfully created new user!", data)
                history.push("/about")
                alert(`Created New User: ${name}`)
            })
            .catch(error => {
                console.log("ERRRRRR", error)
                alert(`Error ${error}`)
            })
        }
    }

    return (
        <div>
            <label className={'input-name-lbl'}>NAME</label><br></br>
            <input
                className={'input'}
                type={'name'}
                id={'name'}
                placeholder={'Enter Name...'}
                onChange={(e)=>setName(e.target.value)}
            /><br></br>
            <label className={'input-name-lbl'}>EMAIL</label><br></br>
            <input
                className={'input'}
                type={'email'}
                id={'email'}
                placeholder={'Enter Email...'}
                onChange={(e)=>setEmail(e.target.value)}
            /><br></br>
            <label className={'input-name-lbl'}>PASSWORD</label><br></br>
            <input 
                className={'input'}
                type={'password'}
                id={'password'}
                placeholder={'Enter Password...'}
                onChange={(e)=>setPassword(e.target.value)}
            />
            <br></br>
            <button className={'login-btn'} onClick={attemptRegister}>Register</button>
        </div>
    )
}

export default RegisterComponent