import React, {useState, useEffect} from 'react';
import './App.css';
import AudioTrack from './components/AudioTrack'
import Player from "react-howler-player";
import axios from 'axios'

function Tracks() {

    const [tracks, settracks] = useState([])

    useEffect(() => {
        fetchItems();
    }, []);

    const fetchItems = () => {
        settracks() // make sure its empty upon loading
        // Fetch Tracks Here
        axios
        .get('/gettracks')
        .then(data=> {
            
            const allTracks = []

            const dataKeys = Object.keys(data.data)
            dataKeys.map(d=>allTracks.push(data.data[d]))
            
            settracks(allTracks)
            //settracks(data)
        })
        .catch(error => {
            console.log(error)
        })
    }

    return (
        <div className="App">
            <h2 className={'page-title'}>TRACKS</h2>
            <div>
            <div className={'side-margin'}>
            { !tracks ? 'Loading' :
                tracks.map(track=>(
                    <div key={track.location}>
                        {<AudioTrack className={'no-top-margin '}
                            trackUrl={track.location}
                            trackTitle={track.title}
                            
                        />}
                    </div>
                    )
                )}
            </div>
            </div>
        </div>
    )
}



export default Tracks