import React from 'react';
import '../App.css';
import { Link } from 'react-router-dom'
import axios from 'axios'
import Cookie from "js-cookie"
import { useHistory } from "react-router-dom";
import { url } from '../config'

function Nav() {

    const history = useHistory();

    const logout = () => {
        Cookie.remove('token')
        history.push("/login");
        alert('logged out')
    }

    // Checks token serverside then sends back success or failure
    function postRoute(e) {
        const targetRoute = e.target.id

        const token =  Cookie.get("token") ? Cookie.get("token") : null;
        let data = {token: token}
           axios
            .post(`${url}/checkAuth`, data) // data to send
            .then(response => response)
            .then(data=> {
                if (data.data != "failure") {
                    if (targetRoute == "login") {alert("You Are Already Logged In")
                    } else {
                        console.log('Token passed sucessfully', data.data)
                        history.push(`/${targetRoute}`);
                    }
                    
                } else {
                    console.log("No token exists")
                    history.push("/login");
                    alert('Please Login to Access Site')
                }
            })
            .catch(error => {
                console.log(error)
        })
    }

    return (
        <nav>
            <h1>SUPER DUPER SOUNDS</h1>
            <ul className='nav-links'>
                <li><button className={'nav-link'} id={'about'} onClick={postRoute}>About</button></li>
                <li><button className={'nav-link'} id={'tracks'} onClick={postRoute}>Tracks</button></li>
                <li><button className={'nav-link'} id={'uploadsound'} onClick={postRoute}>Upload Sounds</button></li>
                <li><button className={'nav-link'} id={'login'} onClick={postRoute}>Login</button></li>
                <li><button className={'nav-link'} onClick={logout}>Logout</button></li>
            </ul>
        </nav>
    )
}

export default Nav