import React, {useState, useEffect} from 'react';
import '../../App.css';
import '../../css/Login.css'
import Fetchy from "../../fetch/useFetch"
import axios from 'axios'
import Cookie from "js-cookie"
import { useHistory } from "react-router-dom";
import { url } from '../../config'

function LoginComponent() {

    const [email, setEmail] = useState('')
    const [password, setPassword] = useState('')
    const history = useHistory();

    const attemptLogin=()=>{
        let data = {email: email, password: password}
        axios
        .post(`${url}/signIn`, data)
        .then(response => response)
        .then(data=> {
            console.log(data.data)
            // checking that the data code did not represent a failed attempt
            if (data.data.idToken && data.code != "auth/wrong-password") {
                // successfully logged in   
                console.log("Set Token", data.data.idToken)
                Cookie.set("token", data.data.idToken);
                history.push("/about")
            } else {
                alert('Wrong Passcode or Email...')
            }
        })
        .catch(error => {
            console.log(error)
        })
    }

    return (
        <div>
            <label className={'input-name-lbl'}>EMAIL</label><br></br>
            <input
            required
                className={'input'}
                type={'email'}
                id={'email'}
                placeholder={'Enter Email...'}
                onChange={(e)=>setEmail(e.target.value)}
            /><br></br>
            <label className={'input-name-lbl'}>PASSWORD</label><br></br>
            <input 
                required
                className={'input'}
                type={'password'}
                id={'password'}
                placeholder={'Enter Password...'}
                onChange={(e)=>setPassword(e.target.value)}
            />
            <br></br>
            <button className={'login-btn'} onClick={attemptLogin} >Login</button>
        </div>
    )
}



export default LoginComponent