import React, { useState } from 'react';
import './css/Login.css'
import { Link, withRouter } from 'react-router-dom'

import RegisterComponent from './components/login&register/RegisterComponent'
import LoginComponent from './components/login&register/LoginComponent'

function Login() {

    const [isLogin, setLogin] = useState(true) // default logging in screen.
    const [name, setName] = useState('')
    const [email, setEmail] = useState('')
    const [password, setPassword] = useState('')

    const invertRegister=()=>{
        let inverted = !isLogin
        setLogin(inverted)
    }

    return (
        <div className={'login-container centered'}>
            <h1>{isLogin ? 'LOGIN' : 'REGISTER'}</h1>
            {isLogin ? <LoginComponent/> : <RegisterComponent/>}
            <button onClick={invertRegister} className={'toggle-login-view'}>{isLogin ? 'Go To Register' : 'Go To Login'}</button>
        </div>
    )
}

export default withRouter(Login)